.login-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 0px;
    background-color:#ffffff;
  }
.login-form1 {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
    /* background-color:#ffffff; */
  }
  
  .input-field {
    padding: 10px;
    margin-bottom: 10px;
    width: 90%;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
  }
  
  .submit-button {
    padding: 10px 20px;
    background-color: #1877f2;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    width: 90%;
  }
  
  .submit-button:hover {
    background-color: #1153b8;
    font-size: 16;
    font-weight: bold;
  }
  
  .forgot-password {
    margin-top: 10px;
    color: #1877f2;
    cursor: pointer;
  }

  .card {
    background-color: #f0f2f6;
  }

  .create-account-button {
    padding: 10px 20px;
    background-color: #41b82a;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .create-account-button:hover {
    background-color: #41b82a;
  }
  .cont {
    height: 100%;
  }
  

